import React from "react";
import { Link } from "react-router-dom";

import "./form.css";

class NewGenmai extends React.PureComponent {
  constructor(props) {
    super(props);

    this.state = {
      NewGenmai: {
        title: "",
        body: "",
        createdAt: null,
        updatedAt: null
      },
      saving: false
    };
  }

  async handleGenmaiSave() {
    this.setState({ saving: true });

    const res = await this.props.onSave({ ...this.state.NewGenmai });

    this.props.history.replace(`/genmai/${res.id}`);
  }

  updateValue(e) {
    let { NewGenmai } = this.state;

    this.setState({
      NewGenmai: { ...NewGenmai, [e.target.name]: e.target.value }
    });
  }

  render() {
    const { NewGenmai } = this.state;

    return (
      <div className="NewGenmai-form">
        <h1>玄米投入口</h1>
        <form
          onSubmit={e => {
            e.preventDefault();
            this.handleGenmaiSave();
          }}
        >
          <div className="NewGenmai-form-field">
            <label>Title</label>
            <input
              type="text"
              name="title"
              value={NewGenmai.title}
              onChange={e => this.updateValue(e)}
            />
          </div>
          <div className="NewGenmai-form-field NewGenmai-form-field-text">
            <textarea
              name="body"
              value={NewGenmai.body}
              onChange={e => this.updateValue(e)}
            />
          </div>
          <div className="NewGenmai-form-buttons">
            <input type="submit" value="Save" />
            <Link to={`/`}>キャンセル</Link>
          </div>
        </form>
      </div>
    );
  }
}

export default NewGenmai;
