import React from 'react';

import logo from './img/logo-128.jpg';
import './footer.css';

const footer = () => {
  return (
    <div class='footer'>
      <div class='footer-logo'>
        <p>a3t3mbk.com</p>
        <img src={logo} alt='A3T3-logo' />
      </div>

      <div class='footer-list'>
        <ul>
          <a href='https://www.a3t3mbk.com/'>
            <li>サイト概要</li>
          </a>
          <a href='https://blog.a3t3mbk.com/'>
            <li>ブログ</li>
          </a>
          <a href='https://www.a3t3mbk.com/'>
            <li>お問い合わせ＆使い方</li>
          </a>
          <a href='https://twitter.com/a3t3mbk'>
            <li class='twitter'>
              <i class='fab fa-twitter' />
            </li>
          </a>
        </ul>
      </div>
    </div>
  );
};

export default footer;
