import PouchDB from 'pouchdb';

export default class DB {
  constructor(name) {
    this.db = new PouchDB(name);
  }

  async getAllDoc() {
    let allDoc = await this.db.allDocs({ include_docs: true });
    let res = {};

    allDoc.rows.forEach(n => (res[n.id] = n.doc));

    return res;
  }

  async createGenmai(NewGenmai) {
    console.log(NewGenmai);
    NewGenmai.createdAt = new Date();
    NewGenmai.updatedAt = new Date();

    const res = await this.db.post({ ...NewGenmai });

    return res;
  }

  async deleteGenmai(genmai) {
    await this.db.remove(genmai);
  }

  async createHakumai(NewHakumai) {
    NewHakumai.createdAt = new Date();
    NewHakumai.updatedAt = new Date();

    const res = await this.db.post({ ...NewHakumai });
    // .then(function(response) {
    //   console.log('Success', response);
    // })
    // .then(function(err) {
    //   console.log('Error', err);
    // });

    return res;
  }
  async updateHakumai(NewHakumai) {
    NewHakumai.updatedAt = new Date();

    const res = await this.db.put({ ...NewHakumai });
    return res;
  }
}

