import React from 'react';
import { Link } from 'react-router-dom';

import genmai from './img/genmai.jpg';
import hakumai from './img/onigiri_maru.png';
import './mainpage.css';

const mainpage = () => {
  return (
    <div class='main'>
      <div class='copy-container'>
        <h1>
          A3T3MBKのアプリケーション置き場にようこそ<span>.</span>
        </h1>
        <h2> 白米を貯めるイメージです </h2>
        <h6>1日10回とかの制限はないで</h6>
      </div>
      <div class='contents'>
        <h3 class='section-title'>精米シリーズ</h3>
      </div>
      <div class='contents-item'>
        <Link to='/genmaiIndex' className='contents-cards'>
          <img src={genmai} alt='img of genmai' />
          <p>
            精米<span>（Markdown）</span>リスト
          </p>
          <p>
            Markdownで書かれたHTMLのリンクリストを玄米に見立てて「精米」します。（現状ローカル保存のみ）
          </p>
        </Link>
      </div>
      <div class='contents-item'>
        <Link to='/hakumaiIndex' className='contents-cards'>
          <img src={hakumai} alt='img of hakumai' />
          <p>
            精米された<span>保存済み</span>リスト
          </p>
          <p>
            「精米」された後、白米保存をクリックしたものが保存されています。（現状ローカル保存のみ）
          </p>
        </Link>
      </div>
      <div class='contact-form' />
    </div>
  );
};

export default mainpage;
