import React from "react";
import { Link } from "react-router-dom";
import dayjs from "dayjs";

import "../../list.css";

function renderDate(updatedAt) {
  let d = dayjs(updatedAt);
  return d.format("MMMM D YYYY, HH:mm");
}

const Genmai = ({ genmai: { _id, title, updatedAt } }) => (
  <div className="list-item">
    <h2>
      <Link to={`/genmai/${_id}`}>
        {title ? title : _id} <br />
        <span className="list-date">{renderDate(updatedAt)}</span>
      </Link>
    </h2>
  </div>
);

export default ({ genmai }) => (
  <div className="list">
    {genmai.map(n => (
      <Genmai genmai={n} key={`genmai-${n._id}`} />
    ))}
  </div>
);
