import React from "react";
import GenmaiList from "./genmaiList";

import { Link } from "react-router-dom";

class GenmaiIndex extends React.PureComponent {
  constructor(props) {
    super(props);

    let genmai = Object.values(props.genmai);
    genmai.sort((a, b) => new Date(b.updatedAt) - new Date(a.updatedAt));

    this.state = { genmai };
  }

  render() {
    let { genmai } = this.state;

    if (!genmai.length) {
      return (
        <div className="app-intro">
          <h2>
            最初に使うのは、玄米投入ページです。マークダウンでhttpを含む文章を入れます。
          </h2>
          <p>
            ここから入れます。{" "}
            <Link className="btn" to="/newGenmai">
              投入口
            </Link>
          </p>
          <p>すべての玄米はローカルに保存されます。</p>
        </div>
      );
    }

    return (
      <div>
        <h2>入れた玄米リスト</h2>
        <GenmaiList genmai={this.state.genmai} />
      </div>
    );
  }
}

export default GenmaiIndex;
