import React from "react";
import { Link } from "react-router-dom";
import marked from "marked";
import dayjs from "dayjs";

import "./show.css";

class ShowGenmai extends React.PureComponent {
  renderDate() {
    let d = dayjs(this.props.genmai.updatedAt);
    return d.format("MMMM D YYYY, HH:mm");
  }

  componentWillMount() {
    if (!this.props.genmai) {
      this.props.history.replace(`/`);
      return;
    }
  }

  render() {
    const { genmai } = this.props;

    if (!genmai) {
      return null;
    }

    return (
      <div>
        <h1>{genmai.title}</h1>
        <div className="genmai-created">
          {this.renderDate()}

          <button
            className="btn"
            onClick={e => this.props.onDelete(genmai._id)}
          >
            Delete
          </button>
          <Link
            to="/hakumaiConfirm"
            className="btn"
            onClick={e => this.props.onSeimai(genmai._id)}
          >
            精米する
          </Link>
        </div>
        <div
          className="genmai-body"
          dangerouslySetInnerHTML={{ __html: marked(genmai.body) }}
        />
      </div>
    );
  }
}

export default ShowGenmai;
