import React from 'react';
import { Link } from 'react-router-dom';

import logo from './img/logo-128.jpg';
import './navbar.css';

export default () => (
  <nav className='navbar'>
    <h1>
      <div class='logo'>
        <img src={logo} alt='A3T3-logo' />
      </div>
      <Link to='/' className='navbar-links'>
        A3T3MBK-appLists
      </Link>
      <Link to='/genmaiIndex' className='navbar-links'>
        玄米Index
      </Link>
      <Link to='/hakumaiIndex' className='navbar-links'>
        白米Index
      </Link>
    </h1>
    <span className='navbar-buttons'>
      <Link to='/newGenmai' className='btn'>
        New 玄米
      </Link>
    </span>
  </nav>
);
