import React, { Component } from 'react';
import { BrowserRouter as Router, Route } from 'react-router-dom';

import uuidv3 from 'uuid/v3';
import DB from './db';

import Navbar from './pages/navbar';
import Footer from './pages/footer';
import Mainpage from './pages/mainpage';
import './App.css';

import GenmaiIndex from './components/genmai/genmaiIndex';
import NewGenmai from './components/genmai/newGenmai';
import ShowGenmai from './components/genmai/showGenmai';
import HakumaiConfirm from './components/hakumai/hakumaiConfirm';
import HakumaiIndex from './components/hakumai/hakumaiIndex';
import HakumaiJson from './components/hakumai/hakumaiJson';

class App extends Component {
  constructor(props) {
    super(props);

    const genmaiDb = new DB('genmaiDb');
    const hakumaiDb = new DB('hakumaiDb');
    this.state = {
      genmaiDb,
      hakumaiDb,
      genmai: [],
      hakumai: [],
      hakumaiTemp: [],
      tags: [],
      loading: true
    };
  }

  async componentDidMount() {
    const genmai = await this.state.genmaiDb.getAllDoc();
    const hakumai = await this.state.hakumaiDb.getAllDoc();

    this.setState({
      genmai,
      hakumai,
      loading: false
    });
    // console.log(genmai);
    // console.log(hakumai);
  }

  async handleGenmaiSave(NewGenmai) {
    let res = await this.state.genmaiDb.createGenmai(NewGenmai);
    let { genmai } = this.state;

    NewGenmai._id = res.id;
    NewGenmai._rev = res.rev;

    this.setState({
      genmai: { ...genmai, [res.id]: NewGenmai }
    });

    return res;
  }

  async handleHakumaiSave(newHakumai) {
    let { hakumai } = this.state;
    newHakumai.isHakumai = true;

    let res = await this.state.hakumaiDb.createHakumai(newHakumai);

    console.log(res);

    this.setState({
      hakumai: { ...hakumai, [res.id]: newHakumai }
    });
    return res;
  }

  async handleUpdateHakumai(newHakumai) {
    let { hakumai } = this.state;

    let res = await this.state.hakumaiDb.updateHakumai(newHakumai);

    this.setState({
      hakumai: { ...hakumai, [res.id]: newHakumai }
    });

    return res;
  }

  async handleGenmaiDelete(id) {
    let { genmai } = this.state;
    let genmaiId = genmai[id];

    if (genmai[id] && window.confirm('この玄米を削除してよろしいですか？')) {
      await this.state.genmaiDb.deleteGenmai(genmaiId);

      delete genmai[id];

      this.setState({ genmai });
    }
  }

  async handleSeimai(id) {
    let { genmai, hakumai } = this.state;
    let genmaiId = genmai[id];

    const mdtitles = genmaiId.body.match(/\[(.*)\]/gm);
    // console.log(mdtitles);

    const titles = mdtitles.map(title => title.slice(1, -1));
    // console.log(titles);

    const mdurls = genmaiId.body.match(/\(((http|chrome).*)\)/gm);
    // console.log(urls);

    const urls = mdurls.map(title => title.slice(1, -1));

    const ids = urls.map(url =>
      uuidv3(url, 'e246ff6d401916fed0960b20e0f3463f')
    );
    // console.log(ids);

    const areHakumai = ids.map(id => hakumai.hasOwnProperty(id));
    // console.log(areHakumai);

    const theirSintyoku = ids.map(id => {
      if (hakumai[id]) {
        // console.log(hakumai[id].sintyoku);
        return hakumai[id].sintyoku;
      } else {
        return 10;
      }
    });
    // console.log(theirSintyoku);
    const theirDobs = ids.map(id => {
      if (hakumai[id]) {
        // console.log(hakumai[id].sintyoku);
        return hakumai[id].dob;
      } else {
        return new Date();
      }
    });

    const theirMemos = ids.map(id => {
      if (hakumai[id]) {
        // console.log(hakumai[id].sintyoku);
        return hakumai[id].textMemo;
      } else {
        return '';
      }
    });

    const theirTags = ids.map(id => {
      if (hakumai[id]) {
        // console.log(hakumai[id].tags);
        return hakumai[id].tags;
      } else {
        return false;
      }
    });
    // console.log(theirTags);

    const hakumaiTemp = urls.map((url, i) => ({
      _id: ids[i],
      url: urls[i],
      title: titles[i],
      sintyoku: theirSintyoku[i],
      rating: 3,
      isHakumai: areHakumai[i],
      tags: theirTags[i],
      dob: theirDobs[i],
      textMemo: theirMemos[i]
    }));

    let tagsArr = hakumaiTemp.filter(i => i.tags).map(i => i.tags);
    console.log(Object.keys(tagsArr));

    // console.log(tagsArr.length);

    // console.log(tagsArr[0][0]);
    // console.log(tagsArr[1][0]);
    // console.log(tagsArr[0][1]);
    // console.log(tagsArr[1][1]);

    var ary2 = Array.prototype.concat.apply([], tagsArr);

    // console.log(tagsArr);
    // console.log(ary2);
    this.setState({ tags: ary2 });
    this.setState({ hakumaiTemp });
  }

  renderContent() {
    if (this.state.loading) {
      return <h2>Loading...</h2>;
    }

    return (
      <div className='app-content'>
        <Route exact path='/' component={props => <Mainpage />} />
        <Route
          exact
          path='/GenmaiIndex'
          component={props => (
            <GenmaiIndex {...props} genmai={this.state.genmai} />
          )}
        />
        <Route
          exact
          path='/genmai/:id'
          component={props => (
            <ShowGenmai
              {...props}
              genmai={this.state.genmai[props.match.params.id]}
              onDelete={id => this.handleGenmaiDelete(id)}
              onSeimai={id => this.handleSeimai(id)}
            />
          )}
        />
        <Route
          path='/newGenmai'
          component={props => (
            <NewGenmai
              {...props}
              onSave={genmai => this.handleGenmaiSave(genmai)}
            />
          )}
        />
        <Route
          path='/hakumaiConfirm'
          component={props => (
            <HakumaiConfirm
              {...props}
              hakumai={this.state.hakumai}
              hakumaiTemp={this.state.hakumaiTemp}
              tags={this.state.tags}
              onSave={hakumaiTemp => this.handleHakumaiSave(hakumaiTemp)}
            />
          )}
        />
        <Route
          path='/hakumaiIndex'
          component={props => (
            <HakumaiIndex
              {...props}
              hakumai={this.state.hakumai}
              tags={this.state.tags}
              onSave={hakumaiTemp => this.handleHakumaiSave(hakumaiTemp)}
            />
          )}
        />
        <Route
          path='/HakumaiJson'
          component={props => (
            <HakumaiJson {...props} hakumai={this.state.hakumai} />
          )}
        />
      </div>
    );
  }

  render() {
    return (
      <Router>
        <div className='App'>
          <Navbar />
          {this.renderContent()}
          <Footer />
        </div>
      </Router>
    );
  }
}

export default App;
